    <div class="intro__img intro__img-one" :style="`background-image: url(${introgif})`"></div>
    <div class="intro__img intro__img-two" :style="`background-image: url(${introgif})`"></div>
    <div class="intro__lines"></div>
  </div>
</template>
<script>

const intro = require('@/assets/intro/introanime.gif')

export default {
  name: 'Intro',
  data () {
    return {
      introgif: intro,
      animaaiclass: 'animaii',
      animeDelay: 1000,
      animaai: ''
    }
  },
  mounted () {},
  created () {
    this.addAnimaii()
  },
  methods: {
    addAnimaii () {
      // this.animaai = this.animaaiclass;
      // const rananim = this.animeDelay * (Math.floor(Math.random() * 50) + 2);
      setTimeout(() => {
        this.animaai = this.animaaiclass
        this.removeAnimaii()
      }, 1000)
    },
    removeAnimaii () {
      // this.animaai = '';
      setTimeout(() => {
        this.animaai = ''
        this.addAnimaii()
      }, 2000)
    }
  }
}
</script>
